.trackContainer {
    height: 4rem;
    display: flex;
    flex-direction: column;
    align-items: baseline;
    margin-left: 1rem;
}

.titulo {
    font-size: 1.4rem;
    font-weight: 600;
}

.artista {
    font-size: 1rem;
}

.addButton {
    background-color: rgb(29, 185, 84);
    color: white;
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
    border: 1px solid white;
    font-size: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 1rem;
}

.addButton:hover {
    cursor: pointer;
}

.addButton:active {
    margin-top: 3px;
    margin-left: 3px;
}
.deleteButton {
    background-color: rgb(69, 6, 6);
    color: white;
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
    border: 1px solid white;
    font-size: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 1rem;
}

.deleteButton:hover {
    cursor: pointer;
}

.deleteButton:active {
    margin-top: 3px;
    margin-left: 3px;
}